export default {
	props:{
		tabBars:{
			type:Array,
			default:[]
		},
		center:{
			type:Boolean,
			default:false
		}
	},
	components: {
		
	},
	data() {
		return {
			currentIndex: 0,
			swiperHeight: '',
			tabIndex: 0,
			
		};
	},
	mounted() {
		console.log('123');
		uni.getSystemInfo({
			success: (res) => {
				let height=res.windowHeight-50-5;
				this.swiperHeight=height;
				console.log('res', res,height);
			}
		});
	},
	methods: {
		tabtap(index) {
			this.tabIndex = index;
			this.currentIndex = index;
		},
		tabChange(e) {
			console.log(e);
			if (e.detail.source == 'touch')
				this.tabIndex = e.detail.current;
		}
	}
}
